.button73 {
  appearance: none;
  background-color: #ffffff;
  border-radius: 40em;
  border-style: none;
  box-shadow: teal 0 -12px 6px inset;
  box-sizing: border-box;
  color: #000000;
  cursor: pointer;
  display: inline-block;
  font-family: -apple-system, sans-serif;
  font-size: 0.8rem;
  font-weight: 400;
  letter-spacing: -0.24px;

  outline: none;
  padding: 1rem 1.3rem;
  quotes: auto;
  text-align: center;
  margin-left: 22px;

  text-decoration: none;
  transition: all 0.15s;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  margin-bottom: 5%;
}

.button73:active {
  transform: scale(1.025);
}
